import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { Ghost, UtensilsCrossed, Calendar, Users, ArrowLeft, Instagram } from "lucide-react";

const AboutUs = () => {
  const { i18n } = useTranslation();
  const navigate = useNavigate();
  const lang = i18n.language;

  const content = {
    zh: {
      title: "关于我们",
      intro: "小熊市场是一个连接美食爱好者与优质餐厅的一站式平台。我们整合主理人IP品牌、外卖订餐、活动发现和美食社区，致力于让每一次用餐都成为难忘的体验。无论您是想探索新餐厅、参与美食活动，还是与志同道合的美食家交流，小熊市场都能满足您的需求。",
      featuresTitle: "平台功能",
      features: [
        {
          icon: "ghost",
          title: "幽灵厨房",
          subtitle: "主理人IP品牌展示",
          content: "探索主理人IP品牌，深入了解每位美食创作者的故事与理念。沉浸式的品牌体验，让您感受食物背后的温度与匠心。",
          details: [
            "了解主理人的创业故事与美食哲学",
            "浏览精心设计的品牌主页",
            "发现独特的招牌菜品与制作理念",
            "直达外卖点餐入口"
          ]
        },
        {
          icon: "utensils",
          title: "餐厅私域",
          subtitle: "餐厅官方服务入口",
          content: "一键直达餐厅私域，了解餐厅品牌故事，浏览菜单并完成外卖点单或预约订座。所有服务由餐厅直接提供，我们仅作为连接渠道。",
          details: [
            "进入餐厅官方私域页面",
            "浏览餐厅介绍与品牌故事",
            "外卖订餐与预约订座（餐厅自营）",
            "所有交易由餐厅直接负责"
          ]
        },
        {
          icon: "calendar",
          title: "美食活动",
          subtitle: "精彩活动一手掌握",
          content: "发现周边精彩的美食活动与限时优惠。订阅感兴趣的活动，第一时间获取专属福利和最新动态。",
          details: [
            "浏览热门美食活动与限时优惠",
            "一键订阅感兴趣的活动",
            "接收活动提醒与专属福利",
            "参与互动赢取积分奖励"
          ]
        },
        {
          icon: "users",
          title: "美食社区",
          subtitle: "分享发现交流互动",
          content: "分享您的用餐体验，发布图文动态。与志同道合的美食爱好者交流互动，发现更多隐藏美味。",
          details: [
            "发布图文分享用餐体验",
            "关联餐厅与活动标签",
            "与美食爱好者互动交流",
            "发现社区推荐的隐藏美食"
          ]
        }
      ],
      contactTitle: "联系我们",
      contactContent: "如果您有任何问题、建议或合作意向，欢迎通过 comercial@xiaoxiong.pt 联系我们。我们期待听到您的声音！"
    },
    en: {
      title: "About Us",
      intro: "Xiaoxiong Market is a one-stop platform connecting food lovers with quality restaurants. We integrate owner-branded concepts, takeaway ordering, event discovery, and a food community, dedicated to making every meal an unforgettable experience. Whether you want to explore new restaurants, participate in food events, or connect with like-minded foodies, Xiaoxiong Market has you covered.",
      featuresTitle: "Platform Features",
      features: [
        {
          icon: "ghost",
          title: "Virtual Kitchen",
          subtitle: "Owner Brand Showcase",
          content: "Explore owner-branded concepts and discover the stories behind each culinary creator. Experience immersive brand storytelling that reveals the passion and craftsmanship behind every dish.",
          details: [
            "Learn about owners' entrepreneurial stories and food philosophy",
            "Browse beautifully designed brand homepages",
            "Discover signature dishes and cooking concepts",
            "Direct access to takeaway ordering"
          ]
        },
        {
          icon: "utensils",
          title: "Restaurant Portal",
          subtitle: "Official Restaurant Services",
          content: "Access restaurants' private portals to learn their stories, browse menus, and place orders or reservations. All services are provided directly by restaurants—we simply connect you.",
          details: [
            "Enter official restaurant portal pages",
            "Browse restaurant introductions and brand stories",
            "Takeaway and reservations (operated by restaurants)",
            "All transactions handled directly by restaurants"
          ]
        },
        {
          icon: "calendar",
          title: "Food Events",
          subtitle: "Stay Updated on Activities",
          content: "Discover exciting food events and limited-time offers nearby. Subscribe to activities and be the first to receive exclusive benefits and updates.",
          details: [
            "Browse popular food events and limited offers",
            "One-click subscription to interested events",
            "Receive event reminders and exclusive perks",
            "Participate in interactions to earn points"
          ]
        },
        {
          icon: "users",
          title: "Food Community",
          subtitle: "Share, Discover, Connect",
          content: "Share your dining experiences through posts and photos. Connect with fellow food enthusiasts, exchange recommendations, and uncover hidden gems.",
          details: [
            "Post photos and share dining experiences",
            "Tag restaurants and events",
            "Interact with fellow food lovers",
            "Discover community-recommended hidden gems"
          ]
        }
      ],
      contactTitle: "Contact Us",
      contactContent: "If you have any questions, suggestions, or partnership inquiries, please contact us at comercial@xiaoxiong.pt. We look forward to hearing from you!"
    },
    pt: {
      title: "Sobre Nós",
      intro: "O Xiaoxiong Market é uma plataforma completa que conecta amantes de comida com restaurantes de qualidade. Integramos conceitos de marca do proprietário, encomendas, descoberta de eventos e uma comunidade gastronómica, dedicados a tornar cada refeição uma experiência inesquecível. Quer pretenda explorar novos restaurantes, participar em eventos gastronómicos ou conectar-se com entusiastas, o Xiaoxiong Market tem tudo o que precisa.",
      featuresTitle: "Funcionalidades",
      features: [
        {
          icon: "ghost",
          title: "Cozinha Virtual",
          subtitle: "Vitrine de Marcas",
          content: "Explore conceitos de marca do proprietário e descubra as histórias por trás de cada criador culinário. Experimente uma narrativa de marca imersiva que revela a paixão e o artesanato por trás de cada prato.",
          details: [
            "Conheça as histórias e filosofia dos proprietários",
            "Navegue por páginas de marca bem desenhadas",
            "Descubra pratos exclusivos e conceitos culinários",
            "Acesso direto a encomendas"
          ]
        },
        {
          icon: "utensils",
          title: "Portal do Restaurante",
          subtitle: "Serviços Oficiais do Restaurante",
          content: "Aceda aos portais privados dos restaurantes para conhecer as suas histórias, consultar menus e fazer encomendas ou reservas. Todos os serviços são fornecidos diretamente pelos restaurantes—nós apenas fazemos a ligação.",
          details: [
            "Aceda às páginas oficiais do portal do restaurante",
            "Consulte apresentações e histórias das marcas",
            "Encomendas e reservas (operadas pelos restaurantes)",
            "Todas as transações são tratadas diretamente pelos restaurantes"
          ]
        },
        {
          icon: "calendar",
          title: "Eventos Gastronómicos",
          subtitle: "Fique Atualizado",
          content: "Descubra eventos gastronómicos emocionantes e ofertas por tempo limitado nas proximidades. Subscreva atividades e seja o primeiro a receber benefícios exclusivos.",
          details: [
            "Navegue por eventos populares e ofertas limitadas",
            "Subscrição com um clique em eventos de interesse",
            "Receba lembretes e vantagens exclusivas",
            "Participe em interações para ganhar pontos"
          ]
        },
        {
          icon: "users",
          title: "Comunidade Gastronómica",
          subtitle: "Partilhe, Descubra, Conecte",
          content: "Partilhe as suas experiências gastronómicas através de publicações e fotos. Conecte-se com entusiastas da gastronomia, troque recomendações e descubra tesouros escondidos.",
          details: [
            "Publique fotos e partilhe experiências",
            "Marque restaurantes e eventos",
            "Interaja com outros amantes de comida",
            "Descubra tesouros recomendados pela comunidade"
          ]
        }
      ],
      contactTitle: "Contacte-nos",
      contactContent: "Se tiver alguma questão, sugestão ou interesse em parceria, por favor contacte-nos através de comercial@xiaoxiong.pt. Estamos ansiosos por ouvi-lo!"
    }
  };

  const currentContent = content[lang as keyof typeof content] || content.en;

  const getFeatureIcon = (iconName: string) => {
    const iconProps = { className: "h-8 w-8 text-primary" };
    switch (iconName) {
      case "ghost":
        return <Ghost {...iconProps} />;
      case "utensils":
        return <UtensilsCrossed {...iconProps} />;
      case "calendar":
        return <Calendar {...iconProps} />;
      case "users":
        return <Users {...iconProps} />;
      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen pt-4 pb-12 px-4 bg-secondary/20">
      <div className="container mx-auto max-w-4xl">
        {/* Top navigation row */}
        <div className="flex items-center justify-between mb-4">
          <Button
            variant="ghost"
            size="icon"
            onClick={() => navigate(-1)}
            className="h-10 w-10 rounded-md bg-background shadow-sm border border-border/50"
          >
            <ArrowLeft className="h-5 w-5" />
          </Button>
          <a
            href="https://www.instagram.com/xiaoxiongmarket/"
            target="_blank"
            rel="noopener noreferrer"
            className="h-10 w-10 rounded-md bg-background shadow-sm border border-border/50 flex items-center justify-center active:bg-secondary transition-colors md:hover:bg-secondary"
          >
            <Instagram className="h-5 w-5" />
          </a>
        </div>

        <Card>
          <CardHeader>
            <CardTitle className="text-3xl font-bold">{currentContent.title}</CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* Platform intro */}
            <p className="text-muted-foreground leading-relaxed text-lg">
              {currentContent.intro}
            </p>

            {/* Separator */}
            <Separator className="my-8" />

            {/* Features section */}
            <div>
              <h2 className="text-2xl font-bold mb-6">{currentContent.featuresTitle}</h2>
              <div className="grid grid-cols-1 gap-6">
                {currentContent.features.map((feature, index) => (
                  <Card key={index} className="bg-secondary/30 border-0">
                    <CardContent className="pt-6">
                      <div className="flex flex-col md:flex-row md:items-start gap-4">
                        <div className="flex-shrink-0 p-3 rounded-lg bg-primary/10 self-start">
                          {getFeatureIcon(feature.icon)}
                        </div>
                        <div className="flex-1">
                          <div className="mb-3">
                            <h3 className="font-semibold text-xl">{feature.title}</h3>
                            <p className="text-sm text-primary">{feature.subtitle}</p>
                          </div>
                          <p className="text-muted-foreground leading-relaxed mb-4">
                            {feature.content}
                          </p>
                          <ul className="space-y-2">
                            {feature.details.map((detail, idx) => (
                              <li key={idx} className="flex items-start gap-2 text-sm text-muted-foreground">
                                <span className="text-primary mt-1">•</span>
                                <span>{detail}</span>
                              </li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </div>

            {/* Separator */}
            <Separator className="my-8" />

            {/* Contact section */}
            <div>
              <h2 className="text-xl font-semibold mb-2">{currentContent.contactTitle}</h2>
              <p className="text-muted-foreground leading-relaxed">{currentContent.contactContent}</p>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default AboutUs;
